"use client"

import { useState, useEffect } from "react"
import { Header } from "@/components/layout/header"
import { Footer } from "@/components/layout/footer"
import { SaleTimer } from "@/components/home/sale-timer"
import Image from "next/image"
import Link from "next/link"
import { Filter, ChevronDown } from "lucide-react"
import { Button } from "@/components/ui/button"
import { publicProductsAPI, Product } from "@/lib/api"

const categories = ["All", "Tops", "Bottoms", "Dresses", "Outerwear", "Knitwear", "Accessories"]

export default function ShopPage() {
  const [products, setProducts] = useState<Product[]>([])
  const [selectedCategory, setSelectedCategory] = useState("All")
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    const fetchProducts = async () => {
      try {
        const data = await publicProductsAPI.getAll()
        setProducts(data)
      } catch (error) {
        console.error("Failed to fetch products:", error)
      } finally {
        setIsLoading(false)
      }
    }
    fetchProducts()
  }, [])

  const filteredProducts = selectedCategory === "All"
    ? products
    : products.filter(p => p.category === selectedCategory)

  return (
    <div className="min-h-screen bg-[#E7F0FA]">
      <SaleTimer />
      <Header />
      <main>
        {/* Hero Banner */}
        <section className="relative h-[35vh] sm:h-[40vh] lg:h-[50vh] overflow-hidden">
          <div
            className="absolute inset-0 bg-cover bg-center"
            style={{ backgroundImage: "url('/luxury-fashion-store-interior-sapphire-blue.jpg')" }}
          >
            <div className="absolute inset-0 bg-[#0D2440]/60" />
          </div>
          <div className="relative h-full container mx-auto px-4 lg:px-8 flex items-center justify-center text-center">
            <div>
              <h1 className="text-3xl sm:text-4xl lg:text-6xl font-bold mb-3 sm:mb-4 text-[#E7F0FA]">Shop All</h1>
              <p
                className="text-sm sm:text-lg text-[#E7F0FA]/80 max-w-md mx-auto"
                style={{ fontFamily: "var(--font-body)" }}
              >
                Discover our complete collection of timeless pieces
              </p>
            </div>
          </div>
        </section>

        {/* Filters & Products */}
        <section className="py-8 sm:py-12">
          <div className="container mx-auto px-4 lg:px-8">
            {/* Filter Bar */}
            <div className="flex flex-col gap-4 mb-6 sm:mb-8 pb-4 sm:pb-6 border-b border-[#7BA4D0]/30">
              {/* Categories - Horizontal scroll on mobile */}
              <div
                className="flex gap-2 overflow-x-auto pb-2 -mx-4 px-4 sm:mx-0 sm:px-0 sm:flex-wrap scrollbar-hide"
                style={{ scrollbarWidth: "none" }}
              >
                {categories.map((category) => (
                  <Button
                    key={category}
                    variant={selectedCategory === category ? "default" : "outline"}
                    size="sm"
                    onClick={() => setSelectedCategory(category)}
                    className={`uppercase tracking-wider text-[10px] sm:text-xs whitespace-nowrap shrink-0 ${selectedCategory === category
                        ? "bg-[#2E5E99] hover:bg-[#0D2440] text-[#E7F0FA]"
                        : "border-[#7BA4D0] text-[#2E5E99] hover:bg-[#7BA4D0]/20"
                      }`}
                  >
                    {category}
                  </Button>
                ))}
              </div>
              {/* Filter & Sort */}
              <div className="flex items-center gap-2 sm:gap-4 justify-end">
                <Button
                  variant="outline"
                  size="sm"
                  className="gap-1.5 sm:gap-2 bg-transparent border-[#7BA4D0] text-[#2E5E99] hover:bg-[#7BA4D0]/20 text-xs sm:text-sm h-9"
                >
                  <Filter className="h-3.5 w-3.5 sm:h-4 sm:w-4" />
                  <span className="hidden sm:inline">Filter</span>
                </Button>
                <Button
                  variant="outline"
                  size="sm"
                  className="gap-1.5 sm:gap-2 bg-transparent border-[#7BA4D0] text-[#2E5E99] hover:bg-[#7BA4D0]/20 text-xs sm:text-sm h-9"
                >
                  Sort
                  <ChevronDown className="h-3.5 w-3.5 sm:h-4 sm:w-4" />
                </Button>
              </div>
            </div>

            {/* Product Grid */}
            {isLoading ? (
              <div className="text-center py-12">
                <p className="text-[#2E5E99]">Loading products...</p>
              </div>
            ) : filteredProducts.length === 0 ? (
              <div className="text-center py-12">
                <p className="text-[#2E5E99]">No products found. Add products from the admin panel.</p>
              </div>
            ) : (
              <div className="grid grid-cols-2 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-3 sm:gap-4 lg:gap-6">
                {filteredProducts.map((product) => (
                  <Link key={product.id} href={`/product/${product.id}`} className="group">
                    <div className="relative aspect-[3/4] overflow-hidden rounded-lg bg-white mb-2 sm:mb-4">
                      <Image
                        src={product.image_url || "/placeholder.svg"}
                        alt={product.name}
                        fill
                        className="object-cover transition-transform duration-500 group-hover:scale-105"
                      />
                      {product.status === "Active" && (
                        <span
                          className="absolute top-2 sm:top-3 left-2 sm:left-3 bg-[#2E5E99] text-[#E7F0FA] text-[10px] sm:text-xs px-2 py-0.5 sm:py-1 uppercase tracking-wider rounded"
                          style={{ fontFamily: "var(--font-body)" }}
                        >
                          {product.stock < 20 ? "Low Stock" : "Available"}
                        </span>
                      )}
                      {product.status === "Out of Stock" && (
                        <span
                          className="absolute top-2 sm:top-3 left-2 sm:left-3 bg-red-500 text-white text-[10px] sm:text-xs px-2 py-0.5 sm:py-1 uppercase tracking-wider rounded"
                          style={{ fontFamily: "var(--font-body)" }}
                        >
                          Out of Stock
                        </span>
                      )}
                    </div>
                    <p
                      className="text-[10px] sm:text-xs uppercase tracking-wider text-[#2E5E99] mb-0.5 sm:mb-1"
                      style={{ fontFamily: "var(--font-body)" }}
                    >
                      {product.category}
                    </p>
                    <h3 className="font-medium text-sm sm:text-base mb-0.5 sm:mb-1 text-[#0D2440]">{product.name}</h3>
                    <div className="flex items-center gap-1.5 sm:gap-2" style={{ fontFamily: "var(--font-body)" }}>
                      <span className="text-[#0D2440] text-sm sm:text-base">${product.price.toFixed(2)}</span>
                    </div>
                  </Link>
                ))}
              </div>
            )}

          </div>
        </section>
      </main>
      <Footer />
    </div>
  )
}
